import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { IndexComponent } from './index/index.component';
import { CreateEditomponent } from './create-edit/create-edit.component';
import { StockIndexComponent } from './stock-index/stock-index.component';

const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: 'sellers', component: IndexComponent },
    { path: 'stores', component: IndexComponent },
    { path: 'sellers/create', component: CreateEditomponent },
    { path: 'stores/create', component: CreateEditomponent },
    { path: 'sellers/edit/:id', component: CreateEditomponent },
    { path: 'stores/edit/:id', component: CreateEditomponent },
    {
      path: 'sellers/stock',
      loadChildren: () => import('./stock/stock.module').then(m => m.StockModule),
    },
    // { path: 'sellers/stock', component: StockIndexComponent },
    // { path: 'sellers/stock/pending', component: StockIndexComponent },

  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RoutingModule {
}
