import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ApiModel } from '../../shared/model/api.model';

@Injectable({
  providedIn: 'root'
})
export class WarehousesServices {

  constructor(private http: HttpClient) {
  }

  public getWarehouses(meta?: string) {
    const url = meta ? environment.MAIN_SYSTEM + 'api/dashboard/warehouses?' + meta : environment.MAIN_SYSTEM + 'api/dashboard/warehouses';
    return this.http.get<ApiModel<any>>(url);
  }

  public getWarehousesList($meta = "") {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/warehouses/get-all/for-selection' + $meta;
    console.log(url);
    return this.http.get<any>(url);
  }

  public getWarehouse(id) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/warehouses/' + id;
    return this.http.get(url);
  }

  public CreateWarehouse(newBadge) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/warehouses';
    return this.http.post(url, newBadge);
  }

  public editWarehouse(badge) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/warehouses/update';
    return this.http.post(url, badge);
  }

  public deleteWarehouse(id) {
    const options = {
      headers: new HttpHeaders({
      }),
      body: {
        id: id
      }
    }
    const url = environment.MAIN_SYSTEM + 'api/dashboard/warehouses';
    return this.http.delete(url, options);
  }
}
